@import "../../../bootstrap";

@each $size in $sizing {
    @include breakpoints(p-#{$size}) {
        padding: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(px-#{$size}) {
        padding-left: #{$size * $step};
        padding-right: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(py-#{$size}) {
        padding-top: #{$size * $step};
        padding-bottom: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(pt-#{$size}) {
        padding-top: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(pr-#{$size}) {
        padding-right: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(pb-#{$size}) {
        padding-bottom: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(pl-#{$size}) {
        padding-left: #{$size * $step}; } }
