@import "../../../bootstrap";

$weight_map:
    (thin, 100),
    (extralight, 200),
    (light, 300),
    (normal, 400),
    (medium, 500),
    (semibold, 600),
    (bold, 700),
    (extrabold, 800),
    (black, 900);

@each $name, $weight in $weight_map {
    @include breakpoints(font-#{$name}) {
        font-weight: $weight
    }
}

.font-bolder {
    font-weight: bolder;
}
