@import "../../../bootstrap";

@each $size in $sizing {
    @include breakpoints(m-#{$size}) {
        margin: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(mx-#{$size}) {
        margin-left: #{$size * $step};
        margin-right: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(my-#{$size}) {
        margin-top: #{$size * $step};
        margin-bottom: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(mt-#{$size}) {
        margin-top: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(mr-#{$size}) {
        margin-right: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(mb-#{$size}) {
        margin-bottom: #{$size * $step}; } }

@each $size in $sizing {
    @include breakpoints(ml-#{$size}) {
        margin-left: #{$size * $step}; } }

@include breakpoints(mx-auto) {
    margin-right: auto;
    margin-left: auto; }

@include breakpoints(my-auto) {
    margin-top: auto;
    margin-bottom: auto; }

@include breakpoints(ml-auto) {
    margin-left: auto; }

@include breakpoints(mr-auto) {
    margin-right: auto; }

@include breakpoints(mt-auto) {
    margin-top: auto; }

@include breakpoints(mb-auto) {
    margin-bottom: auto; }
