@import "../../../bootstrap";

@each $gap in $sizing {
    @include breakpoints(row-gap-#{$gap}) {
        grid-row-gap: #{$gap * $step}; } }

@include breakpoints(row-gap-def) {
    grid-column-gap: $grid-row-gap; }


@each $gap in $sizing {
    @include breakpoints(col-gap-#{$gap}) {
        grid-column-gap: #{$gap * $step}; } }

@include breakpoints(col-gap-def) {
    grid-column-gap: $grid-column-gap; }


@each $gap in $sizing {
    @include breakpoints(grid-gap-#{$gap}) {
        grid-gap: #{$gap * $step}; } }

@include breakpoints(grid-gap-def) {
    grid-gap: $grid-column-gap; }
