@import "../../../bootstrap";

@each $spacing in $sizing {
    @include breakpoints(sx-#{$spacing}) {
        & > *:not(:first-child) {
            margin-left: #{$spacing * $step}; } }

    @include breakpoints(sy-#{$spacing}) {
        & > *:not(:first-child) {
            margin-top: #{$spacing * $step}; } } }
