@import "../../../bootstrap";

@each $height in $sizing {
    @include breakpoints(h-#{$height}) {
        height: #{$height * $step}; } }

@include breakpoints(h-screen) {
    height: 100vh; }

@include breakpoints(h-full) {
    height: 100%; }

@include breakpoints(minh-screen) {
    min-height: 100vh; }



@for $i from 1 through 100 {
    @include breakpoints(h-#{$i}vh) {
        height: #{$i}vh; } }


@for $i from 1 through 100 {
    @include breakpoints(h-#{$i}vw) {
        height: #{$i}vw !important; } }



@each $height in $sizing {
    @include breakpoints(h-min-#{$height}) {
        min-height: #{$height * $step}; } }

@include breakpoints(h-min-screen) {
    min-height: 100vh; }
